.card{
    margin-bottom: 24px;
    .material-icons-outlined{
        color: black;
        font-size: 24px;
    }
    @include pad{
        display: flex;
        flex-grow: 1 ;
    }
    &-title{
        padding: 8px 24px;
        border: 1px solid #00000020;
    }
    &-body{
        padding: 16px 24px;
        border: 1px solid #00000020;
        p{
            color: black;
        }
    }
    
}
